html,
body,
#root {
  height: 100%;
}

#root {
  display: flex;
  flex-direction: column;
}

footer {
  margin-top: 1em;
}

.main-content {
  display: flex;
  flex: 1 1;
  flex-direction: column;
}
.loading.m-dark {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.3);
}

.loading.m-relative.m-dark {
    position: absolute;
}

.signal {
    border: 5px solid #333;
    border-radius: 30px;
    height: 30px;
    left: 50%;
    margin: -15px 0 0 -15px;
    opacity: 0;
    position: fixed;
    top: 50%;
    width: 30px;
    -webkit-animation: pulsate 1s ease-out;
            animation: pulsate 1s ease-out;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;

}

.m-dark .signal {
    border: 5px solid #fff;
}

.m-relative .signal {
    position: absolute;
}

@-webkit-keyframes pulsate {
    0% {
        -webkit-transform: scale(.1);
                transform: scale(.1);
        opacity: 0;
    }

    50% {
        opacity: 1;
    }

    100% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
        opacity: 0;
    }
}

@keyframes pulsate {
    0% {
        -webkit-transform: scale(.1);
                transform: scale(.1);
        opacity: 0;
    }

    50% {
        opacity: 1;
    }

    100% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
        opacity: 0;
    }
}

.task-list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex: 1 1;
}

.task-list__list {
  list-style: none;
  padding: 0;
  width: 100%;
}

.task-list__item {
  display: flex;
  padding: .5em 0;
  cursor: pointer;
}

.task-list__title {
  font-weight: bold;
  display: flex;
  padding: .5em 0;
}

.task-list__item:nth-child(odd) {
  background: #ebebeb;
}

.task-list__id,
.task-list__status,
.task-list__name,
.task-list__email {
  padding: 0 .5em;
}

.task-list__name,
.task-list__email {
  flex: 0 0 20%;
  word-wrap: break-word;
  overflow: hidden;
}

.task-list__text {
  flex: 1 1;
  word-wrap: break-word;
  overflow: hidden;
}

.task-item {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.task-item__content {
}

.task-item__title {
  font-weight: bold;
}

.task-item__img {
  max-width: 320px;
  max-height: 240px;
}

